.item-button {
    grid-area: button;
    font-size: 20px;
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
    border-radius: 100px;
    height: 50px;
    width: 50px;
    align-self: center;
}

.checked {
    color: green;
}

.remove {
    color: darkred;
}

.item-link {
    grid-area: image;
    height: 100px;
}

.item-button:hover {
    opacity: 0.8;
    color: darkred;
}

.title {
    font-weight: bold;
    font-size: large;
    grid-area: title;
    justify-self:start;
    margin-top: 0;
    padding-bottom: 5px;
}

.author {
    font-style: italic;
    grid-area: author;
    justify-self: start;
    padding-right: 20px;
    padding-top: 5px;
}

.item-image {
    box-shadow: 2px 2px 2px black;
    grid-area: image;
    height: 100px;
    max-width: 100px;
}

.item-image:hover {
    height: 110px;   
    box-shadow: 4px 4px 4px black;
}

.item-container {
    display: grid;
    grid-template-columns: 50px 1fr 4fr;
    grid-template-rows: 20px 40px 20px;
    column-gap: 5px;
    grid-template-areas: 
      ". image title"
      "button image author"
      ". image .";
    margin-top: 20px;
    margin-bottom: 40px;
}

/* Items in the bookshelf */

.bookshelf-item-container {
    border: 2px solid rgba(0, 0, 0, 0.11);
    border-radius: 20px;
    width: 200px;
    height: 275px;
    display: grid;
    grid-template-columns: 20px auto;
    grid-template-rows: 20px 150px 50px 50px;
    grid-template-areas: 
    "button image"
    ". image"
    "title title"
    "author author";

    justify-items: center;
    align-items: start;
    margin-right: 10px;
    margin-bottom: 10px;
    padding-top: 5px;
}

.bookshelf-item-container .title {
    font-weight: bold;
    font-size: large;
    grid-area: title;
    justify-self:start;
    margin: 0 10px;
}

.bookshelf-item-container .author {
    grid-area: author;
    justify-self: start;
    padding-top: 5px;
    margin-top: 15px;
    margin-bottom: 0px;
    margin-left: 10px;

}

.bookshelf-item-container .item-image {
    box-shadow: 2px 2px 2px black;
    grid-area: image;
    height: 140px;
    max-width: 140px;
}

.bookshelf-item-container .item-image:hover {
    height: 150px;   
    box-shadow: 4px 4px 4px black;
}

.bookshelf-item-container .item-link {
    grid-area: image;
    height: 140px;
}

.bookshelf-item-container .item-button {
    grid-area: button;
    font-size: 20px;
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
    padding: 20px;
}

/*--------------------------------*/

.results-container {
    width: 600px;
    height: 400px;
    overflow-y: auto;
    margin: auto;
    border: 1px solid black;
    border-radius: 2%;
    text-align: left;
}

.bookshelf-container {
    width: 900px;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    text-align: left;
}

ul {
    padding: 0;
}

/* Scrollbar Style */

/* Works on Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: rgb(0, 0, 0) rgb(211, 211, 211);
  }
  
  /* Works on Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 10px;
  }
  
  *::-webkit-scrollbar-track {
    background: rgb(211, 211, 211);
    border-radius: 20px;
  }
  
  *::-webkit-scrollbar-thumb {
    background-color: rgb(0, 0, 0);
    border-radius: 20px;
    border: 3px solid rgb(211, 211, 211);
  }

  *::-webkit-scrollbar-button {
    height: 12px;
  }
